.profile_section{
  text-align: center;
  width: 100%

}


.profile_section h3{
  box-shadow: 0 4px 12px 0 rgb(0 0 0 / 5%);
  padding: 10px;
}

.profile_section .profile-subsection {
  margin: 5%;

display: flex;
}


.profile_section .profile-subsection .profile-image {
  width: 35%;
  float: left;
  text-align: right;
  padding: 0px 20px;
  border-right: 5px solid #ffe17c;

}

.profile_section .profile-subsection .profile-form {
  width: 65%;
  text-align:left;
  padding: 20px 100px;
}


.profile_section .profile-subsection .profile-form .MuiGrid-align-items-xs-center{
  align-items: start;
}

@media (max-width: 767px) {
  /* .profile_section {margin: auto;} */
  .profile_section .profile-subsection {
    display: block;
    }
  .profile_section .profile-subsection .profile-image{
    text-align: center;
    width: 100%;
    border-right: none;
    padding: 20px 20px 20px 20px;
    border-bottom: 5px solid #ffe17c;
  }

  .profile_section .profile-subsection .profile-image img{
    width: 100%;
    height: auto;
    /* padding: 5% */

  }
  .profile_section .profile-subsection .profile-form {
    /* width: 100%; */
    text-align:left;
    /* padding: 20px 20px; */
  }
  .profile_section .profile-subsection .profile-form .MuiGrid-align-items-xs-center{
    align-items: center;
  }
  }


  .profile_section .profile-subsection .profile-form .extra-section{
    border-top: 2px solid #b4b4b4;
    margin: 20px 0px;
  }
.book-info-root {
  margin: 50px;
  height: 950px;
  box-shadow: 0 19px 100px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
}

.book-info-root .book-info-outer-section {
  width: 100%;
  height: 100%;
  display: flex;
}

.book-info-root .book-info-outer-section .background-left {
  width: 40%;
  float: left;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    250deg,
    rgb(17, 1, 54) 30%,
    rgb(26, 9, 121) 50%,
    rgb(58, 155, 219) 100%
  );
  background-repeat: no-repeat;
  background-size: 70% 100%;
  vertical-align: middle;
  display: flex;
}

.book-info-root .book-info-outer-section .background-left img {
  margin-left: auto;
  margin-top: auto;
  margin-bottom: auto;
  display: block;
  height: 65%;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.book-info-root .book-info-outer-section .background-right {
  width: 60%;
  padding: 30px;
}

.book-info-root .book-info-outer-section .background-right .book-info-inner {
  width: 100%;
}

.book-info-root
  .book-info-outer-section
  .background-right
  .book-info-inner
  .MuiPaper-elevation20 {
  box-shadow: none;
}

.book-info-root
  .book-info-outer-section
  .background-right
  .book-info-inner
  .title {
  font-family: "Domine", serif;
  font-size: 60px;
  color: rgb(207, 207, 207);
}

.book-info-root
  .book-info-outer-section
  .background-right
  .book-info-inner
  .author {
    margin: 20px auto;
  font-family: "Domine", serif;
  font-size: 20px;
  color: rgb(201, 201, 201);
}

.book-info-root
  .book-info-outer-section
  .background-right
  .book-info-inner
  .description {
  font-family: "Domine", serif;
  font-size: 20px;
  color: rgb(119, 119, 119);
  margin: 20px auto 20px;
 height: 520px;
 overflow-y: scroll;
}

.book-info-root
  .book-info-outer-section
  .background-right
  .book-info-inner
  .published-date {
  font-family: "Domine", serif;
  margin: 20px auto;
  font-size: 20px;
  color: rgb(119, 119, 119);
}

